<template>
  <div class="app-register">
      <Header></Header>
     	<div class="mui-ter">
        <div class="mui-card-header">
          <h3>欢迎注册</h3>
          <span>点击上传图像</span>
          <label for="myimgs">
            <img class="mui-tou" src="../../img/icon/tou.png"/>
          </label>
          <input id="myimgs" style="display:none" type="file" name="file" @change="changeFile"/>
        </div>
		    <div class="mui-card-content">
			    <div class="mui-card-content-inner">
            <form>
              <span>用户名:</span>
              <input type="text" placeholder="请输入用户名" v-model="name" @blur.prevent="checkName"/> 
               <span>密码:</span>
              <input type="password" placeholder="请输入密码" v-model="pwd" />
               <span>确认密码:</span>
              <input type="password" placeholder="请输入确认密码" v-model="upwd" />
               <span>手机号:</span>
              <input type="text" value="" placeholder="请输入手机号" v-model="phone" />
               <span>昵称:</span>
              <input type="text" placeholder="请输入昵称" v-model="uname" />
              <mt-button class="mui-large" size="large" @click="handleReg">注册</mt-button>
            </form>
			    </div>
		    </div>
		  </div>
  </div>  
</template>

<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/gation.vue'
export default {
    components: {
    Header
  },
    data(){
        return {
            name:"",
            pwd:"",
            upwd:"",
            phone:"",
            uname:"",
            cm:"",
            isSumit:false 
        }
    },
    methods: {
      checkName(){
        var name=this.name;
        this.axios.get("existsName",{params:{name}}).then(result=>{
         // console.log(result);
          var det=result.data.code;
          if(det==1){
            this.isSumit=true;
            Toast("欢迎注册")
          }else{
            this.isSumit=false;
            Toast("用户名已存在")
          }
        })
      },
     handleReg(){
       if(this.isSumit==false){
         Toast("用户名格式不符合要求");
         return
       }
       var name=this.name;
       var uname=this.uname;
       var pwd=this.pwd;
       var upwd=this.upwd;
       var phone=this.phone;
       var cm=this.cm;
       //正则
       var regname=/^[a-z0-9_]{3,8}$/i;
       var regphone=/^1[3-8]\d{9}$/;
       var reguname=/^[a-z0-9_]{3,12}$/;
       if(!regname.test(name)){
        Toast("用户名为3~8位的数字和字母");
        return
       }
       if(!regname.test(pwd)){
         Toast("密码为3~8位的数字和字母");
         return
       }
       if(pwd!=upwd){
         Toast("两次密码不一致");
         return
       }
       if(!regname.test(uname)){
         Toast("昵称为3~12的数字和字母");
         return
       }
       this.axios.get("register",{params:{name,pwd,phone,uname,cm}}).then(result=>{
         if(result.data.code>=1){
           //Toast(result.data.msg);
           Toast('注册成功~');
           // 注册后页面跳转
           this.$router.push("/page");
         }else{
           //Toast(result.data.msg)
           Toast('注册失败~');
         }
       })
     } ,
     //上传图片
     changeFile(e){
       this.file=e.target.files[0];
       var fd=new FormData();
       fd.append("file",this.file);
       let config = {
         headers:{'Content-Type':'multipart/form-data'}
       };
       this.axios.post("upload",fd,config).then(res=>{
         //console.log(res);
         this.cm=res.data.dd;
         Toast(res.data.msg)
       })
     }
    }
}
</script>

<style>
.app-register{
    width:100%;
    height:36.4rem;
    top:0.3rem;
    background-image:url(../../img/back02.jpg);
}
.mui-ter{
  margin:1rem;
  height:33rem;
  /* background:rgba(3,3,3,0.5); */
  background: rgba(92, 85, 85, 0.637);
  position:absolute;
  top:5%;
  /*color:#2894FF;*/
  color:#fff;
}
.mui-large{
  background: -webkit-linear-gradient(top, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 100%) !important;
  
}
/* .mui-card-content-inner{
  /* color:#2894FF !important; 
  color:#000 !important;
} */
.mui-tou{
  width:4rem;
  height:4rem;
}
.mui-card-header>span{
  color:#fff;
  font-size:0.5rem;
  margin:-2.5rem 0 0 5rem;
}

.mui-ter .mui-card-content span{
  color:rgb(255, 255, 255);
  font-weight: 700;
   font-size: 0.8rem;
}

</style>


