<template>
    <!--国风页面布局-->
    <div class="app-customs">
        <header class="mui-bar mui-bar-nar app-header">
            <a v-on:click="back" class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <div class="mui-diti">
                <input @click="getpos" id="mysrch" type="type" class="mui-clear" placeholder="输入您想查找的内容" />
                <img src="../../img/icon/sousuo.png"/>
            </div>
            <router-link to="/channel" class="mui-icon mui-icon-bars mui-pull-right"></router-link>
        </header>
            <!--模块一-->
        <div v-for="item of list">
            <div  class="mui-card">
                <router-link :to="`/particulars/:${item.lid}`">
                <div  class="mui-card-content-inner">
                    <span>{{item.title}}</span>
                </div>
                <div class="mui-wind">
                    <div>
                        <img :src="item.jmg"/>
                    </div>
                    <div>
                        <img :src="item.cmg"/>
                    </div>
                    <div>
                        <img :src="item.vmg"/>
                    </div>
                </div>
                <div class="mui-pan">
                    <span>{{item.uname}}&nbsp;&nbsp;{{item.ment}}评论&nbsp;&nbsp;{{item.ctime | datetimeFilter}}</span>
                </div>
                </router-link>
            </div>
        </div>
        <mt-button size="large" @click="getMore">加载更多</mt-button>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
export default {
    data(){
      return {
        msg:"",
        pageIndex:0, //当前页码
        pageSize:4,  //页大小
        pageCount:1,  //总页数
        list:[]
      }
  },
  created(){
    this.getMore();
  },
  methods: {
    getMore(){
        this.pageIndex++;
        var pno=this.pageIndex;
        var ps=this.pageSize;
        //3.创建变量url
        //4.发送ajax请求
        this.axios.get("tom",{params:{
            pno,pageSize:ps
        }}).then(result=>{
            //console.log(result);
            //5.获取返回数据 追加
            var rows=this.list.concat(result.data.data);
            this.list=rows;
            //console.log(this.list)
            if(result.data.data==''){
                Toast("没有更多了~");
            }
        })
    },
    //搜索框
    getpos(){
        this.$router.push("/title");
        },
    back(){
        this.$router.go(-1);//返回上一级
    }
  }
}
</script>
<style>
.mui-card-content-inner{
    padding:5px !important;
    color:#000;
}
.mui-wind{
     height:6rem;
     display:flex;
     align-items: center;
}
.mui-wind div{
    margin:0.3rem;
}
.mui-wind img{
    width:100%;
    height:5rem;
}
/*评论span*/
.mui-pan{
    margin:0.2rem;
    font-size:0.3rem;
    color:#666;
}
</style>


