<template>
    <div class="mui-video">
        <Header></Header>
        <div id="box" class="mui-card" v-for="item of list">
            <div>
                <p>更新时间：{{item.ctime | datetimeFilter}}</p>
            </div>
            <video id="v3" :src="item.deo" webkit-playsinline="true"
            x5-video-player-type="h5"
            x5-video-player-fullscreen="true"
            x5-video-orientation="portaint" controls>
            </video>
            <div class="mui-vire arrow">
                <span>{{item.uname}}</span>
                <span><a href="javascript:;">{{item.ment}}</a>&nbsp;点播</span>
            </div>
        </div>
        <mt-button size="large" @click="getMore">加载更多</mt-button>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/search.vue'
export default {
  components: {
    Header
  },
   data(){
      return {
        msg:"",
        pageIndex:0, //当前页码
        pageSize:2,  //页大小
        pageCount:1,  //总页数
        list:[]
      }
  },
  methods: {
        getMore(){
            this.getCom();
        },
        getCom(){
            this.pageIndex++;
            var pno=this.pageIndex;
            var ps=this.pageSize;
            //3.创建变量url
            //4.发送ajax请求
            this.axios.get("video",{params:{
                pno,pageSize:ps
            }}).then(result=>{
                //console.log(result);
                //5.获取返回数据 追加
                var rows=this.list.concat(result.data.data);
                this.list=rows;
                //console.log(this.list)
                if(result.data.data==''){
                    Toast("没有更多了~");
                }
            })
        }
  },
  created(){
        this.getCom();
    }
}
</script>
<style>
#box #v3{
    width:100%;
    height:12rem;
}
.mui-vire{
    width:100%;
    height:3rem;
    line-height:3rem;
    font-size:1rem;
}
.mui-vire span{
    margin-left:2rem;
}
.mui-vire>span:nth-child(2){
    margin-left:-8rem;
}
.arrow{
    margin-left:-1rem;
}
</style>


