<template>
    <div>
        <header class="mui-bar mui-bar-nar app-header">
            <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" v-on:click="back"></a>
            <div class="mui-diti">
                <input id="mysrch" type="type" class="mui-clear" placeholder="输入您想查找的内容" v-model="kwords" />
                <img @click="hanleso" src="../../img/icon/sousuo.png"/>
            </div>
            <router-link to="/channel" class="mui-icon mui-icon-bars mui-pull-right"></router-link>
        </header>
        <div v-for="item of list">
            <div  class="mui-card">
                <router-link :to="`/particulars/:${item.lid}`">
                <div  class="mui-card-content-inner">
                    <span>{{item.title}}</span>
                </div>
                <div class="mui-wind">
                    <div>
                        <img :src="item.fmg"/>
                    </div>
                    <div>
                        <img :src="item.bmg"/>
                    </div>
                    <div>
                        <img :src="item.cmg"/>
                    </div>
                </div>
                <div class="mui-pan">
                    <span>{{item.uname}}&nbsp;&nbsp;{{item.ment}}评论&nbsp;&nbsp;{{item.ctime | datetimeFilter}}</span>
                </div>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import Header from '@/components/header/search.vue'
export default {
	components: {
    Header
  },
    data(){
        return {
            list:[],
            kwords:""
        }
    },
    methods:{
        back(){
            this.$router.go(-1);//返回上一级
        },
        //搜索框功能
        hanleso(){
            var kwords=this.kwords;
            this.axios.get("sou",{params:{kwords}}).then(result=>{
                //console.log(result);
                this.list=result.data
            })
        }  
    }
}
</script>
<style>
    
</style>


