<template>
<div class="app-login">
    <Header></Header>
    <div class="mui-der">
        <div class="mui-card-header">
          <h3>欢迎登录</h3>
        </div>
        <div class="mui-card-content">
            <div class="mui-card-content-inner">
                <form>
                <span>用户名:</span>
                    <input type="text" value=""
                    placeholder="请输入用户名" v-model="name"/> 
                    <span>密码:</span>
                    <input type="password" value=""
                    placeholder="请输入密码" v-model="pwd"/>
                    <mt-button class="mui-large" size="large" @click.prevent="headleLogin">登录</mt-button>
                </form>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {Toast} from "mint-ui";
import Header from '@/components/header/gation.vue'
export default {
	components: {
    Header
  },
    data(){
        return {
            name:"",
            pwd:""
        }
    },
    methods:{
        headleLogin(){
            //1.获取用户名，密码
            var n=this.name;
            var p=this.pwd;
            //2:验证
            var reg = /^[a-z0-9_]{3,12}$/i;
            //验证用户名
            if(!reg.test(n)){
            Toast("用户名格式不正确");
            return;
            }
            //验证密码
            if(!reg.test(p)){
            Toast("密码格式不正确");
            return;
            }
            //4:发送ajax请求
            var url="login?name="+n;
      url +="&pwd="+p;
            this.axios.get(url).then(result=>{
                // console.log(result.data);
                if(result.data.code == 1){
                    //console.log(result);
                    //获取请求过来的用户的id并保存
                    var lid=result.data.lid;
                    //将用户的id保存在sessionStorage中
                    //保存在sessionStorage中的用户id可以在其他页面都可获取
                    sessionStorage.setItem("lid",lid);
                    Toast("登录成功~");
                    this.$router.push("/home");
                }else{
                    Toast("用户名或密码不正确")
                }
            })
        }
    },
}
</script>

<style>
.app-login{
    width:100%;
    height:36.4rem;
    top:0.3rem;
    background-image:url(../../img/back01.jpg);
}
.mui-der{
    margin:1rem;
    position:absolute;
    /* background:rgba(3,3,3,0.5); */
    background:rgba(92, 85, 85, 0.637);
    border-radius:0.5rem;
    top:20%;
    /* color:	#2894FF; */
    color: #fff;
}
.mui-large{
     background: -webkit-linear-gradient(top, rgb(255, 255, 255) 0%, rgb(255, 255, 255) 100%) !important;
}
.mui-der mui-card-content-inner input{
    /* color:#333; */
    color: #fff;
}

.mui-der .mui-card-content{
    height: 13.5rem;
}

.mui-der .mui-card-content span{
  color:rgb(255, 255, 255);
  font-weight: 700;
  font-size: 1rem;
}

</style>


