import Vue from 'vue'
import Router from 'vue-router'
import HelloContainer from "./components/HelloWorld.vue"
import home from "./components/test/home.vue" //首页
import recom from "./components/content/recom.vue"  //推荐页
import customs from "./components/content/customs.vue" //国风页
import NBA from "./components/content/NBA.vue"  //NBA页面
import video from "./components/content/video.vue"  //视屏页面
import channel from "./components/content/channel.vue" //右上角按钮页面
import particulars from "./components/particulars/particulars.vue"  //内容详情页
import page from "./components/ego/page.vue"  //登录
import register from "./components/ego/register.vue"  //注册
import install from "./components/ego/install.vue"  //登录后个人主页
import my from "./components/ego/my.vue"  //未登录个人主页
import search from "./components/header/search.vue" //导航栏
import gation from "./components/header/gation.vue" //头顶上的导航栏
import available from "./components/particulars/available.vue" //暂无
import comment from "./components/particulars/comment.vue"
import title from "./components/particulars/title.vue"

Vue.use(Router)

export default new Router({
  routes: [
    {path:'/home',component:home},
    {path:'/recom',component:recom},
    {path:'/customs',component:customs},
    {path:'/NBA',component:NBA},
    {path:'/video',component:video},
    {path:'/channel',component:channel},
    {path:'/particulars/:lid',component:particulars},
    {path:'/page',component:page},
    {path:'/register',component:register},
    {path:'/install',component:install},
    {path:'/my',component:my},
    {path:'/search',component:search},
    {path:'/gation',component:gation},
    {path:'/available',component:available},
    {path:'/comment/:lid',component:comment},
    {path:'/title',component:title},
    {path:'/*',component:home},
  ]
})



