<template>
   <div id="setting" class="mui-page">
			<!--页面标题栏开始-->
        <header id="header" class="mui-bar mui-bar-nav">
			<a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" v-on:click="back"></a>
			<h1 class="mui-title">个人主页</h1>
	</header>
			<!--页面标题栏结束-->
			<!--页面主内容区开始-->
        <div class="mui-page-content">
            <div class="mui-scroll-wrapper">
                <div class="mui-scroll">
                    <ul class="mui-table-view mui-table-view-chevron">
                        <li class="mui-table-view-cell mui-media">
                            <a class="mui-navigate-right" href="#account">
                                <img class="mui-media-object mui-pull-left head-img" id="head-img" :src="list[0].cm">
                                <div class="mui-media-body">
                                    {{list[0].uname}}
                                    <p class='mui-ellipsis'>账号:{{list[0].name}}</p>
                                </div>
                            </a>
                        </li>
                    </ul>
                    <ul class="mui-table-view mui-table-view-chevron">
                        <li class="mui-table-view-cell">
                            <a href="#account" class="mui-navigate-right">账号与安全</a>
                        </li>
                    </ul>
                    <ul class="mui-table-view mui-table-view-chevron">
                        <li class="mui-table-view-cell">
                            <a href="#notifications" class="mui-navigate-right">新消息通知</a>
                        </li>
                        <li class="mui-table-view-cell">
                            <a href="#privacy" class="mui-navigate-right">隐私</a>
                        </li>
                        <li class="mui-table-view-cell">
                            <a href="#general" class="mui-navigate-right">通用</a>
                        </li>
                    </ul>
                    <ul class="mui-table-view mui-table-view-chevron">
                        <li class="mui-table-view-cell">
                            <a href="#about" class="mui-navigate-right">关于The Social Front <i class="mui-pull-right update">S4.0.2</i></a>
                        </li>
                    </ul>
                    <ul class="mui-table-view">
                        <li class="mui-table-view-cell" style="text-align: center;">
                            <a @click="quit">退出登录</a>
                        </li>
					</ul>
				</div>
			</div>
		</div>
     </div> 
</template>
<script>
export default {
    data(){
        return {
            list:[{cm:""}]
        }
    },
    created() {
        this.axios.get("tepp").then(result=>{
            console.log(result);
            this.list=result.data
        })
    },
    methods:{
        quit(){
            this.axios.get("quit").then(result=>{
                this.$router.push("/my");
            })
        },
        back(){
            this.$router.go(-1);//返回上一级
        },
        
    }
}
</script>
<style>
.mui-scroll-wrapper{
    top:3.2rem !important;
}
.mui-table-view span.mui-pull-right {
    color: #999;
}
.mui-table-view{
    margin-top:1.3rem !important;
}
</style>


