<template>

    <div class="mui-part">
        <!--导航栏-->
        <Header></Header>
        <!--主体内容-->
        <div class="mui-usert">
            <h3 class="part-title">{{list[0].title}}</h3>
            <div>
                <div class="part-one">
                    <span>{{list[0].aont}}</span>
                </div>
                <img class="part-two" :src="list[0].fmg"/>
                <div class="part-one">
                    <span>{{list[0].bont}}</span>
                </div>
                <img class="part-two" :src="list[0].bmg"/>
                <div class="part-one">
                    <span>{{list[0].cont}}</span>
                </div>
                <img class="part-two" :src="list[0].cmg"/>
                <div class="part-one">
                    <span>{{list[0].dont}}</span>
                </div>
                <img class="part-two" :src="list[0].dmg"/>
                <div class="part-one">
                    <span>{{list[0].eont}}</span>
                </div>
                <img class="part-two" :src="list[0].emg"/>
            </div>
            <!--评论区-->
            <hr/>
        </div>
        <div class="app-newsinfo">
            <!--评论显示区-->
            <div class="cmt-item" v-for="(item,i) of list1">
                第{{i}}楼：{{item.ctime | datetimeFilter}}
                <div class="cmt-body">
                    {{item.title}}
                </div>
            </div>
            <router-link class="mui-ping" :to="`/comment/:${list[0].lid}`">评论</router-link>
        </div>
    </div>
</template>
<script>
import comment from "../particulars/comment.vue"
import Header from '@/components/header/gation.vue'
export default {
    components: {
    Header
  },
    data(){
        return {
            list:[{title:"",lid:""}],
            list1:[]
        }
    },
    created() {
        var lid=this.$route.params.lid;
        lid=lid.slice(1);
        //console.log(lid+"123");
        this.axios.get("add_com",{params:{lid}}).then(res=>{
            this.list=res.data;
            //console.log(res)
        });
        this.axios.get("cri",{params:{lid}}).then(res=>{
            //console.log(res);
            this.list1=res.data;
        })
    },

}
</script>
<style>
.mui-usert{
    text-align:center;
}
   .part-title{
       margin:1rem;
   } 
   .part-one{
       margin:0.5rem;
   }
   .part-two{
       height:15rem;
   }
   .cmt-list{
        margin:1rem 0;
    }
    .cmt-list .cmt-item{
        margin:0.5rem !important;
    }
    .app-newsinfo{
        margin:0.5rem;
    }
    .mui-dian{
        font-size:1.2rem;
    }
    .mui-ping{
        display:block;
        width:100%;
        height:2rem;
        margin:0 auto;
        text-align:center;
        line-height:2rem;
        font-size:1.2rem;
        color:#000;
        border-radius:0.3rem;
        background: -webkit-linear-gradient(top, #fff 0%, #fff 100%);
    }
    hr{
        height:0.2rem;
        background:red;
        margin-top:1rem;
    }
</style>


