<template>
    <div class="app-comment">
        <Header></Header>
        <div class="mui-ment">
        <div>
            <h3>发表您想说的:</h3>
            <input class="mui-input" placeholder="请输入内容" maxlength="120" v-model="msg"/>
            <mt-button size="large" @click="addComment">发表</mt-button>
        </div>
        </div>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/gation.vue'
export default {
	components: {
    Header
  },
    data(){
        return {
            msg:""
        }
    },
    methods: {
        addComment(){
            var nid=this.$route.params.lid;
            var lid=nid.slice(1);
            var content=this.msg;
            var size=content.trim().length;
            //console.log(content,lid);
             if(size == 0){//判断如果内容为空，提示消息
                Toast("吐槽内容不能为空");
                return;
             }
             this.axios.get("tic",{params:{lid,content}}).then(result=>{
                 //console.log(result);
                 if(result.data.code>=1){
                     Toast(result.data.msg);
                     this.$router.go(-1);//返回上一级;
                 }else{
                     Toast(result.data.msg);
                 }
             })
        }
    },
}
</script>
<style>
.app-comment{
    width:100%;
    height:36.4rem;
    top:0.3rem;
    background-image:url(../../img/back01.jpg);
}
.mui-ment{
    width:100%;
    height:36.4rem;
    background:rgba(3,3,3,0.6);
}
.mui-ment>div{
    margin:1.5rem;
    padding-top:10rem;
    color:#000;
}

.mui-ment>div>h3{
    color:#fff;
}

.mui-input{
    width:100%;
    height:3rem;
    margin-bottom:1rem;
}
</style>


