<template>
    <div class="mui-nba">
        <Header></Header>
        <!--NBA详情页面-->
        <div class="mui-card" v-for="item of list">
            <div class="mui-card-content-inner">
                <span>{{item.title}}</span>
            </div>
            <div class="mui-shorl">
                <router-link :to="`/particulars/:${item.lid}`"><img :src="item.jmg" /></router-link>
            </div>
                <span>{{item.uname}}&nbsp;&nbsp;{{item.ment}}评论&nbsp;&nbsp;{{item.ctime | datetimeFilter}}</span>
        </div>
        <mt-button size="large" @click="getMet">加载更多</mt-button>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/search.vue'
export default {
    components: {
    Header
  },
    data(){
      return {
        msg:"",
        pageIndex:0, //当前页码
        pageSize:2,  //页大小
        pageCount:1,  //总页数
        list:[]
      }
  },
  created(){
    this.getMet();
  },
  methods: {
        getMet(){
            this.pageIndex++;
            var pno=this.pageIndex;
            var ps=this.pageSize;
            //3.创建变量url
            //4.发送ajax请求
            this.axios.get("nba",{params:{
                pno,pageSize:ps
            }}).then(result=>{
                //console.log(result);
                //5.获取返回数据 追加
                var rows=this.list.concat(result.data.data);
                this.list=rows;
                if(result.data.data==''){
                  Toast('没有更多了~');
                }
                //console.log(this.list)
            })
        }
  }
}
</script>
<style>
.mui-card-content-inner{
    color:#333 !important;
    font-size:0.9rem;
}
</style>


