<template>
    <div class="app-home">
        <header class="mui-bar mui-bar-nar app-header">
            <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left"></a>
            <div class="mui-diti">
                <input @click="getpos" id="mysrch" type="type" class="mui-clear" placeholder="输入您想查找的内容" />
                <img src="../../img/icon/sousuo.png"/>
            </div>
            <router-link to="/channel" class="mui-icon mui-icon-bars mui-pull-right"></router-link>
        </header>
        <!--轮播图-->
        <mt-swipe :auto="2500" class="swipe">
            <mt-swipe-item v-for="item in list1" :key="item.id">
                <img :src="item.img_url"/>
            </mt-swipe-item>
        </mt-swipe>
        <!--第一个:九宫格-->
       <div class="mui-content">
		    <ul class="mui-table-view mui-grid-view mui-grid-9">
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/recom">
                        <img src="../../img/all.png"/>
                        <div class="mui-media-body">推荐</div></router-link></li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/customs">
                        <img src="../../img/country.png"/>
                        <div class="mui-media-body">国风</div></router-link></li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/video">
                        <img src="../../img/video.png"/>
                        <div class="mui-media-body">小视频</div></router-link></li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/NBA">
                        <img src="../../img/NBA.png"/>
                        <div class="mui-media-body">NBA</div></router-link></li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/video">
                        <img src="../../img/recreation.png"/>
                        <div class="mui-media-body">娱乐</div></router-link></li>
                <li class="mui-table-view-cell mui-media mui-col-xs-4 mui-col-sm-3"><router-link to="/available">
                    <img src="../../img/more.png"/>
                    <div class="mui-media-body">更多</div></router-link></li>   
		    </ul> 
		</div> 
        <div class="waibao" v-for="item of list">
            <div class="mui-card">
                <div class="mui-shorl">
                    <router-link :to="`/particulars/:${item.lid}`"><img :src="item.mg"/></router-link>
                </div>
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p>Posted on January {{item.ctime | datetimeFilter}}</p>
                        <p style="color: #333;">{{item.title}}</p>
                    </div>
                </div>
                <div class="mui-card-footer">
                    <a class="mui-card-link">{{item.uname}}</a>
                    <a class="mui-card-link arrow">更多</a>
                </div>
            </div>
        </div>
        <mt-button size="large" @click="getHom">加载更多</mt-button>
    </div>
</template>

<script>
import {Toast} from  "mint-ui";
export default {
    data(){
        return {
            list1:[],
            msg:"",
            pageIndex:0, //当前页码
            pageSize:2,  //页大小
            pageCount:1,  //总页数
            list:[]
        }
    },
    created() {
        this.getImages();
        this.getHom();
    },
    methods:{
        getImages(){
            //完成一个功能，获取服务器端的轮播图片
            //1.发送ajax请求
            var url="getImages";
            this.axios.get(url).then(result=>{
                //console.log(result.data);
                this.list1=result.data;
                console.log(this.list1);
            })
            //2.获取返回数据保存list
        },
        getHom(){
        this.pageIndex++;
        var pno=this.pageIndex;
        var ps=this.pageSize;
        //3.创建变量url
        //4.发送ajax请求
        this.axios.get("com",{params:{
            pno,pageSize:ps
        }}).then(result=>{
            //console.log(result);
            //5.获取返回数据 追加
            var rows=this.list.concat(result.data.data);
            this.list=rows;
            if(result.data.data==''){
              Toast('没有更多了~');
            }
            //console.log(this.list)
        })
    },
    //搜索框
    getpos(){
        this.$router.push("/title");
    }
    }

}
</script>

<style>
.app-home .swipe{
    height:12.5rem;
}

.app-home .swipe img{
    height:12.5rem;
}

.app-home .mint-swipe img{
    width:100%;
}
.app-home .mui-content ul{
    background:#fff;
}
.app-home .mui-grid-view.mui-grid-9  li{
    border:0;
}
.app-home .mui-content img{
    width:60px;
    height:60px;
}
.mui-table-view:first-child{
    margin-top:0 !important;
    background:#FDFFFF !important;
}
</style>


