<template>
    <div class="app-school">
        <Header></Header>
        <div class="waibao" v-for="item of list">
            <div class="mui-card">
                <div class="mui-shorl">
                    <router-link :to="`/particulars/:${item.lid}`"><img :src="item.mg"/></router-link>
                </div>
                <div class="mui-card-content">
                    <div class="mui-card-content-inner">
                        <p>Posted on January {{item.ctime | datetimeFilter}}</p>
                        <p style="color: #333;">{{item.title}}</p>
                    </div>
                </div>
                <div class="mui-card-footer">
                    <a class="mui-card-link">{{item.uname}}</a>
                    <a class="mui-card-link arrow">更多</a>
                </div>
            </div>
        </div>
        <mt-button size="large" @click="getCom">加载更多</mt-button>
    </div>
</template>
<script>
import {Toast} from  "mint-ui";
import Header from '@/components/header/search.vue'
export default {
  components: {
    Header
  },
  data(){
      return {
        msg:"",
        pageIndex:0, //当前页码
        pageSize:2,  //页大小
        pageCount:1,  //总页数
        list:[]
      }
  },
  created(){
     this.getCom();
  },
  methods: {
    getCom(){
        this.pageIndex++;
        var pno=this.pageIndex;
        var ps=this.pageSize;
        //3.创建变量url
        //4.发送ajax请求
        this.axios.get("com",{params:{
            pno,pageSize:ps
        }}).then(result=>{
            //console.log(result);
            //5.获取返回数据 追加
            var rows=this.list.concat(result.data.data);
            this.list=rows;
            if(result.data.data==''){
                Toast("没有更多了~");
            }
            //console.log(this.list)
            
        })
    },
  }
}
</script>
<style>
.mui-shorl img{
    width:100%;
    height:9.5rem;
}
    /*右侧箭头*/
.arrow{
  display:flex;
  justify-content: space-between;
  align-items: center;
}
.arrow::after{
  content:"";
  width:0.6rem;
  height:0.6rem;
  border-top:2px solid #007aff;
  border-right:2px solid #007aff;
  transform:rotate(45deg);
}
</style>


