<template>
    <header class="mui-bar mui-bar-nar app-header">
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" v-on:click="back"></a>
        <div class="mui-diti">
            <input id="mysrch" type="type" class="mui-clear" placeholder="输入您想查找的内容" v-model="kwords" />
            <img @click="hanleso" src="../../img/icon/sousuo.png"/>
        </div>
        <router-link to="/channel" class="mui-icon mui-icon-bars mui-pull-right"></router-link>
    </header>
</template>
<script>
export default {
    data(){
        return {
            kwords:""
        }
    },
    methods:{
        back(){
            this.$router.go(-1);//返回上一级
        },
        hanleso(){
            var kwords=this.kwords;
            this.axios.get("sou",{params:{kwords}}).then(result=>{
                //console.log(result); 
            })
        }
        
    }
}
</script>
<style>
/*顶部导航栏*/
.app-container{
    padding:0;
}
.mui-bar{
    background: -webkit-linear-gradient(top, #ACD6FF 0%, #C4E1FF 100%) !important;
}
/*搜索框父元素*/
.mui-diti{
    width:75%;
    height:4rem;
    position:absolute;
    margin-left:10%;
}
/*搜索框搜索图标*/
.mui-diti img{
    width:1.3rem;
    position:absolute;
    top:0.7rem;
    margin-left:-2rem;
}
/*搜索框input*/
.mui-clear{
    margin-top:0.4rem!important;
    width:100%;
    height:2rem;
    font-size:1.2rem;
    border-radius:0.6rem;
    padding-left:1.5rem;
    border:1px solid transparent;
} 
</style>


