<template>
    <!--顶部导航栏-->
 <div class="app-container">
    
    <router-view></router-view>
    <!--底部tabbar-->
    <nav class="mui-bar mui-bar-tab">
        <router-link to="/" class="mui-tab-item mui-active">
            <span class="mui-icon">
                <img src="./img/icon/shou02.png"/>
            </span>
            <span class="mui-tab-label">首页</span>
        </router-link>
        <router-link to="/channel" class="mui-tab-item">
            <span class="mui-icon">
                <img src="./img/icon/fen01.png" />
            </span>
            <span class="mui-tab-label">分类</span>
        </router-link>
        <router-link to="/recom" class="mui-tab-item">
            <span class="mui-icon">
                <img src="./img/icon/rei01.png"/>
            </span>
            <span class="mui-tab-label">热门</span>
        </router-link>

        <a @click="myCinst" class="mui-tab-item">
            <span  class="mui-icon"><img src="./img/icon/wo01.png"/></span>
            <span class="mui-tab-label">我的主页</span>
        </a>

    </nav>
    
 </div>
</template>

<script>
export default {  
data(){
    return {
        
    }
},
methods: {
    myCinst(){
        //单击事件，获取lid
        //var lid=sessionStorage.getItem("lid");
        this.axios.get("http://127.0.0.1:3000/hui").then(result=>{
            console.log(result.data.code)
        
        //判断lid是否为null，
        if(result.data.code==-1){
            //如果为null跳转当前未登录页面
            this.$router.push("/my");
        }else{
            //否则跳转登陆后的个人页面
            this.$router.push("/install");
        }
        })
    },
}

}
</script>

<style>
    .mui-bar-nar{
        top:0rem !important;
    } 
   .app-container{
     padding-top:40px !important;
     padding-bottom:50px;
     overflow-x:hidden;
   }

 .mui-bar-tab .mui-tab-item-tao.mui-active {
    color: #007aff;
 }
.mui-bar-tab .mui-tab-item-tao {
    display: table-cell;
    overflow: hidden;
    width: 1%;
    height: 50px;
    text-align: center;
    vertical-align: middle;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #929292;
}
.mui-bar-tab .mui-tab-item-tao .mui-icon {
    top: 3px;
    width: 24px;
    height: 24px;
    padding-top: 0;
    padding-bottom: 0;
}

.mui-bar-tab .mui-tab-item-tao .mui-icon~.mui-tab-label {
   font-size:11px;
   display:block;
   overflow:hidden;
   text-overflow:ellipsis;

}
.app-container{
    padding:0;
}
/*底部导航栏*/
.mui-tab-item img{
    width:1.3rem;
}
.mui-tab-item span{
    color:#000;
    font-size:1rem;
}
.app-container{
    margin-bottom:44px;
}
</style>



